import { Controller, Node } from '@ali/4ever-cangjie';
import { isInReadOnlyRefBlock } from '../../utils/logic';

/**
 * 如果块存在于一个只读引用块中，则不允许拖动 
 * @param controller 
 * @param node 
 * @returns 
 */
export function disableDrag(controller: Controller, node: Node) {
  return isInReadOnlyRefBlock(controller.value, node);
}
